.container {
    align-self: center;
    display: flex;
    justify-content: center;
    align-items: stretch;

    h1, p, pre {
        margin: 0px;
    }

    h1 {
        align-self: center;
        font-size: xxx-large;
    }

    span {
        padding: 5px 0px;
        margin-left: 10px;
        border-left: 2px solid #445;
        padding-left: 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        max-width: 450px;

        p:first-of-type {
            margin-bottom: 4px;
        }

        pre {
            margin-top: 20px;
            color: #99a;
            font-family: 'Droid Sans Mono', 'Courier New', Courier, monospace;
            font-size: small;
            font-weight: bold;
        }
    }
}

@media screen and (max-width: 750px) {
    .container {
        flex-direction: column;
        max-width: 550px !important;

        span {
            padding: 0px 5px;
            padding-top: 10px;
            margin: 0px;
            margin-top: 10px;
            border: none;
            border-top: 2px solid #445;
            max-width: none;
        }
    }
}